@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color:li;
  font-family: 'Source Sans Pro', sans-serif;
}

body{
  width: 100vw;
  max-width: 1400px;
  min-width: 400px;
}

:root{
--white: #ffffff;
--lightblue: linear-gradient(180deg, rgba(10, 212, 250, 0.86) 0%, rgba(37, 187, 241, 0.86) 100%);
--grey: #D9D9D9;
--lightgrey: #F2F2F2;
--textLightGrey: #A3A3A3;
--textDarkGrey: #616161;
}

.flex-column{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.flex-row{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.normal-text{
  font-size: 20px;
  color: #616161;
  color: var(--textDarkGrey);
}

.center{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

@media (min-width: 600px) {
  .normal-text{
    font-size: 24px;
  }
}

.section{
    background: var(--lightgrey);
    padding: 3% 5%;
    width: 100%;
}
.nav{
    padding: 1% 2%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.container-user{
    width: 215px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.button{
    border-radius: 20.5px;
    padding: 1% 5%;
    background: var(--grey);
}

@media (min-width: 600px) {
    .button{
        padding: 2% 7%;
    }
  }
  
.header{
    height: 170px;
}

.img-portal{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.principal-text{
    color: var(--white);
    font-size: 40px;
    -webkit-transform: translateY(-135%);
            transform: translateY(-135%);
    z-index: 9999;
    margin-left: 10%;
}

  @media (min-width: 600px) {
    .header{
        height: 260px;
    }
  }

@media (min-width: 1000px) {
    .header{
        height: 365px;
    }
  }
.footer{
    border-bottom: 1.5px solid var(--grey);
}
